import dotenv from "dotenv";

const initializeEnv = () => {
const baseEnv = dotenv.config().parsed;
  const env = dotenv.config({
    path: `.env.${process.env.NODE_ENV}`,
  }).parsed;
  const localEnv = dotenv.config({
    path: `.env.${process.env.NODE_ENV}.local`,
  }).parsed;
}

export const init = () => {
  initializeEnv();
};
